import React from 'react';
import { useUserStore, UserState } from '../store/user';
import { useLeagueStore } from '../store/league';
import { League, platformsOptions } from '../types/League';
import { navigate } from 'gatsby';
import { APP_ROUTES } from '../constants';
import { DialogModal } from './DialogModal';
import { EditLeagueForm } from './LeagueForm/EditLeagueForm';
import { AddAndClaimLeagueForm } from './LeagueForm/AddAndClaimLeagueForm';
import { CreateMergedLeagueForm } from './MergeLeaguesForm/CreateMergedLeagueForm';
import { EditMergedLeagueForm } from './MergeLeaguesForm/EditMergedLeagueForm';
import { MdMoreVert } from 'react-icons/md';
import * as Ariakit from '@ariakit/react';
import { PopoverMenu } from './PopoverMenu';

const thClasses = 'px-6 py-3';
const tdClasses = 'px-6 py-4';

const LeaguesTable: React.FC<{ showAddLeagueForm: string }> = ({ showAddLeagueForm }) => {
    const [isModalOpen, setIsModalOpen] = React.useState(false);
    const [selectedLeague, setSelectedLeague] = React.useState<League | null>(null);
    const [mergedLeagueToEdit, setMergedLeagueToEdit] = React.useState<League | null>(null);
    const [isMergeModalOpen, setIsMergeModalOpen] = React.useState(false);
    const [isAddLeagueModalOpen, setIsAddLeagueModalOpen] = React.useState(false);

    const user = useUserStore((state: UserState) => state.user);
    const { leagues, getLeagues } = useLeagueStore((state) => {
        return {
            leagues: state.leagues,
            getLeagues: state.getLeagues,
        };
    });
    const [leaguesToMerge, setLeaguesToMerge] = React.useState<League[]>([]);

    React.useEffect(() => {
        if (typeof window !== 'undefined') {
            const urlParams = new URLSearchParams(window.location.search);
            const mode = urlParams.get('mode');

            if (mode === 'add') {
                setIsAddLeagueModalOpen(true);
            }
        }
    }, [showAddLeagueForm]);

    function handleEdit(league: League) {
        if (league.platform === 'merged') {
            setMergedLeagueToEdit(league);
        } else {
            setSelectedLeague(league);
            setIsModalOpen(true);
        }
    }

    function onCloseEditModal() {
        setIsModalOpen(false);
    }

    function onCloseAddLeagueModal() {
        setIsAddLeagueModalOpen(false);
        if (typeof window !== 'undefined') {
            const url = window.location.href;
            const newUrl = url.replace('?mode=add', '');
            window.history.replaceState({}, '', newUrl);
        }
    }

    function handleLeagueClick(league: League) {
        if (leaguesToMerge.some((item) => item.id === league.id)) {
            setLeaguesToMerge(leaguesToMerge.filter((item) => item.id !== league.id));
        } else {
            if (leaguesToMerge.length === 2) {
                const newLeaguesToMerge = [...leaguesToMerge];
                newLeaguesToMerge.shift();
                newLeaguesToMerge.push(league);
                setLeaguesToMerge(newLeaguesToMerge);
            } else {
                setLeaguesToMerge([...leaguesToMerge, league]);
            }
        }
    }

    function onCloseMergeModal() {
        setIsMergeModalOpen(false);
        setLeaguesToMerge([]);
    }

    let sortedLeagues = leagues
        ? leagues.sort((a, b) => {
              if (a.createdAt && b.createdAt) {
                  return b.createdAt.seconds - a.createdAt.seconds;
              } else {
                  return 0;
              }
          })
        : [];

    return (
        <>
            <div className="flex gap-3">
                <button className="base-button" onClick={() => setIsAddLeagueModalOpen(true)}>
                    Add League
                </button>
                <button
                    className="base-button"
                    disabled={leaguesToMerge.length !== 2}
                    onClick={() => setIsMergeModalOpen(true)}
                >
                    Merge leagues
                </button>
            </div>
            {leagues && leagues.length > 0 && (
                <div className="relative mt-4 overflow-x-auto shadow sm:rounded-md">
                    <table
                        aria-label={`Leagues of ${user?.displayName}`}
                        className="w-full px-6 py-4 text-sm text-left text-gray-500"
                    >
                        <thead className="text-xs uppercase text-primary-800 bg-primary-100">
                            <tr>
                                <th className={thClasses}>Merge</th>
                                <th className={thClasses}>Name</th>
                                <th className={thClasses}>Platform</th>
                                <th className={thClasses}>League Id</th>
                                <th className={thClasses}>Consolation Games</th>
                                <th className={thClasses}>Status</th>
                                <th className={`${thClasses} sr-only`}>Actions</th>
                            </tr>
                        </thead>
                        <tbody>
                            {sortedLeagues.map((league, i) => {
                                return (
                                    <tr key={i} className="bg-white border-b">
                                        <td
                                            className={tdClasses}
                                            onClick={(e) => e.stopPropagation()}
                                        >
                                            <input
                                                type="checkbox"
                                                checked={leaguesToMerge.some(
                                                    (item) => item.id === league.id
                                                )}
                                                onChange={() => {
                                                    handleLeagueClick(league);
                                                }}
                                                disabled={league.platform === 'merged'}
                                            />
                                        </td>
                                        <td>
                                            {league.slug || `${league.platform}-${league.leagueid}`}
                                        </td>
                                        <td className={tdClasses}>
                                            {platformsOptions[league.platform].label}
                                        </td>
                                        <td className={tdClasses}>{league.leagueid}</td>
                                        <td className={tdClasses}>
                                            {league.consolationGames ? 'Yes' : 'No'}
                                        </td>
                                        <td className={`${tdClasses} capitalize`}>
                                            {league?.status || 'Active'}
                                        </td>
                                        <td>
                                            <div className="flex gap-2">
                                                <PopoverMenu
                                                    button={
                                                        <span className="flex items-center justify-center w-10 transition-colors rounded-full aspect-square hover:bg-gray-100">
                                                            <MdMoreVert size={24} />
                                                        </span>
                                                    }
                                                >
                                                    <Ariakit.MenuItem
                                                        onClick={() => handleEdit(league)}
                                                    >
                                                        Edit
                                                    </Ariakit.MenuItem>
                                                    <Ariakit.MenuItem
                                                        onClick={() =>
                                                            navigate(
                                                                `${APP_ROUTES.MERGE_LEAGUES}/${
                                                                    league?.slug ||
                                                                    `${league.platform}-${league.leagueid}`
                                                                }`
                                                            )
                                                        }
                                                    >
                                                        Merge Duplicates
                                                    </Ariakit.MenuItem>
                                                    <Ariakit.MenuItem
                                                        onClick={() =>
                                                            navigate(
                                                                `${APP_ROUTES.LEAGUE_SETTINGS}/${
                                                                    league?.slug ||
                                                                    `${league.platform}-${league.leagueid}`
                                                                }`
                                                            )
                                                        }
                                                    >
                                                        Settings
                                                    </Ariakit.MenuItem>
                                                </PopoverMenu>
                                            </div>
                                        </td>
                                    </tr>
                                );
                            })}
                        </tbody>
                    </table>
                </div>
            )}
            <DialogModal isOpened={isModalOpen} onClose={onCloseEditModal} aria-label="Edit League">
                {selectedLeague && isModalOpen && (
                    <EditLeagueForm
                        league={selectedLeague}
                        onEditEnd={() => setIsModalOpen(false)}
                    />
                )}
            </DialogModal>
            <DialogModal
                isOpened={isMergeModalOpen}
                onClose={onCloseMergeModal}
                aria-label="Merge Leagues"
            >
                <CreateMergedLeagueForm
                    league1={leaguesToMerge[0]}
                    league2={leaguesToMerge[1]}
                    onSubmit={onCloseMergeModal}
                />
            </DialogModal>
            {isAddLeagueModalOpen && (
                <DialogModal
                    isOpened={isAddLeagueModalOpen}
                    onClose={onCloseAddLeagueModal}
                    aria-label="Add League"
                >
                    <AddAndClaimLeagueForm
                        onSubmit={() => {
                            onCloseAddLeagueModal();
                            if (user) {
                                getLeagues(user);
                            }
                        }}
                    />
                </DialogModal>
            )}
            <DialogModal
                isOpened={!!mergedLeagueToEdit}
                onClose={() => setMergedLeagueToEdit(null)}
                aria-label="Edit Merged League"
            >
                {mergedLeagueToEdit && (
                    <EditMergedLeagueForm
                        league={mergedLeagueToEdit}
                        onSubmit={() => setMergedLeagueToEdit(null)}
                    />
                )}
            </DialogModal>
        </>
    );
};

export default LeaguesTable;
