import React from 'react';
import { RouteComponentProps, useParams } from '@reach/router';
import { useLeagueStore } from '../store/league';
import { generateDocumentLeagueId } from '../utils/leagueUtils';
import { League, LeagueUser, LeagueYear } from '../types/League';
import UsersForm from '../components/LeagueSettings/UsersForm';
import YearsForm from '../components/LeagueSettings/YearsForm';
import { useBanner } from '../context/BannerContext';
import { getChangedValues } from '../utils';

type LeagueSettingsParams = {
    slug: string;
};

export const LeagueSettings: React.FC<RouteComponentProps> = () => {
    const { slug }: LeagueSettingsParams = useParams();
    const { showBanner } = useBanner();
    const { leagues, updateLeague } = useLeagueStore((state) => {
        return {
            leagues: state.leagues,
            updateLeague: state.updateLeague,
        };
    });

    let league1: League | undefined;
    let league2: League | undefined;

    let currentSlug: string = '';
    const currentLeague = leagues?.find((league) => {
        currentSlug = league.slug || generateDocumentLeagueId(league);
        return currentSlug === slug;
    });
    const isMergedAndValid = currentLeague?.platform === 'merged' && !!currentLeague?.leagues;

    if (isMergedAndValid) {
        const [league1Id, league2Id] = currentLeague.leagues as string[];
        league1 = leagues?.find((league) => {
            return league.id === league1Id;
        });

        league2 = leagues?.find((league) => {
            return league.id === league2Id;
        });
    }

    const [currentLeagueValues, setCurrentLeagueValues] = React.useState<League | undefined>(
        currentLeague
    );
    const [league1CurrentValues, setLeague1CurrentValues] = React.useState<League | undefined>(
        league1
    );
    const [league2CurrentValues, setLeague2CurrentValues] = React.useState<League | undefined>(
        league2
    );

    const formRef = React.useRef<HTMLFormElement>(null);

    if (!currentLeagueValues || !currentLeague) return null;

    return (
        <div className="container">
            <div className="flex justify-between pb-2 border-b">
                <h2>Settings for {currentSlug}</h2>
                <button
                    className="base-button"
                    onClick={() => {
                        if (!formRef.current?.reportValidity()) return;

                        if (
                            league1 &&
                            JSON.stringify(league1) !== JSON.stringify(league1CurrentValues)
                        ) {
                            const updatedValues: Partial<League> = getChangedValues(
                                league1,
                                league1CurrentValues as League
                            );
                            updateLeague(league1.id, updatedValues);
                        } else if (
                            league2 &&
                            JSON.stringify(league2) !== JSON.stringify(league2CurrentValues)
                        ) {
                            const updatedValues: Partial<League> = getChangedValues(
                                league2,
                                league2CurrentValues as League
                            );
                            updateLeague(league2.id, updatedValues);
                        } else if (
                            JSON.stringify(currentLeague) !== JSON.stringify(currentLeagueValues)
                        ) {
                            const updatedValues: Partial<League> = getChangedValues(
                                currentLeague,
                                currentLeagueValues as League
                            );
                            updateLeague(currentLeague.id, updatedValues);
                        }

                        showBanner({
                            text: 'Settings saved',
                            type: 'success',
                        });
                    }}
                >
                    Save
                </button>
            </div>
            <form className="flex flex-col gap-4 md:flex-row" ref={formRef}>
                <div className="w-full mt-4 md:w-1/2">
                    <UsersForm
                        league={currentLeagueValues}
                        onUserChange={(user: LeagueUser) => {
                            const updatedUsers = currentLeagueValues.users?.map((currentUser) => {
                                if (currentUser.ids.toString() === user.ids.toString()) {
                                    return user;
                                }
                                return currentUser;
                            });

                            const updatedLeague = {
                                ...currentLeague,
                                users: updatedUsers,
                            };

                            setCurrentLeagueValues({ ...updatedLeague });
                        }}
                    />
                    <section>
                        <YearsForm
                            league1={league1CurrentValues || currentLeagueValues}
                            league2={league2CurrentValues}
                            onYearChange={(leagueId: string, year: LeagueYear) => {
                                // find which league to update based on leagueId between league1CurrentValues, league2CurrentValues, and currentLeagueValues

                                const leagues = [
                                    league1CurrentValues,
                                    league2CurrentValues,
                                    currentLeagueValues,
                                ];
                                const updatedLeagues = leagues.map((league) => {
                                    if (league?.id === leagueId && league.years) {
                                        return {
                                            ...league,
                                            years: league.years.map((y) =>
                                                y.year === year.year
                                                    ? { ...y, enabled: !y.enabled }
                                                    : y
                                            ),
                                        };
                                    }
                                    return league;
                                });
                                const updatedLeague = updatedLeagues.find(
                                    (league) => league?.id === leagueId
                                );

                                if (updatedLeague) {
                                    if (updatedLeague.id === league1CurrentValues?.id) {
                                        setLeague1CurrentValues(updatedLeague);
                                    } else if (updatedLeague.id === league2CurrentValues?.id) {
                                        setLeague2CurrentValues(updatedLeague);
                                    } else if (updatedLeague.id === currentLeagueValues?.id) {
                                        setCurrentLeagueValues(updatedLeague);
                                    }
                                }
                            }}
                        />
                    </section>
                </div>
            </form>
        </div>
    );
};
