import React from 'react';
import { League, LeagueInput } from '../../types/League';
import MergeLeaguesForm, { GeneratedPartiallyMergedLeague } from './index';
import { generateDocumentLeagueId, generateMergedLeagueId } from '../../utils/leagueUtils';
import { useLeagueStore } from '../../store/league';
import { useUserStore, UserState } from '../../store/user';
import { User } from '../../types/User';
import services from '../../services';

interface Props {
    league1: League;
    league2: League;
    onSubmit: () => void;
}

export const CreateMergedLeagueForm: React.FC<Props> = ({ league1, league2, onSubmit }) => {
    const user = useUserStore((state: UserState) => state.user);
    const { createLeague, getLeagues } = useLeagueStore((state) => {
        return {
            createLeague: state.createLeague,
            getLeagues: state.getLeagues,
        };
    });

    async function handleSubmit(partialMergedLeague: GeneratedPartiallyMergedLeague) {
        const mergedLeague: LeagueInput = {
            ...partialMergedLeague,
            leagueid: generateMergedLeagueId(league1, league2),
            leagues: [league1.id, league2.id],
        };

        try {
            await createLeague(mergedLeague);
            await getLeagues(user as User);
            await services.leagueService.sendLeagueMergedNotification(
                user?.email || user?.displayName || '',
                generateDocumentLeagueId(league1),
                generateDocumentLeagueId(league2),
                mergedLeague.slug as string
            );
            onSubmit();
        } catch (err: any) {
            throw err;
        }
    }

    return <MergeLeaguesForm onSubmit={handleSubmit} />;
};
