import React from 'react';
import { Router } from '@reach/router';
import Dashboard from '../app/Dashboard';
import AppNavbar from '../components/AppNavbar';
import PrivateRoute from '../components/PrivateRoute';
import { APP_ROUTES } from '../constants';
import EmailCodes from '../app/EmailCodes';
import MergeLeagues from '../app/MergeLeagues';
import { LeagueSettings } from '../app/LeagueSettings';

const App = () => {
    return (
        <>
            <AppNavbar />
            <main className="pt-3 app-container">
                <Router>
                    <PrivateRoute component={Dashboard} path={APP_ROUTES.DASHBOARD} />
                    <EmailCodes path={APP_ROUTES.AUTH_CODES} />
                    <PrivateRoute
                        component={MergeLeagues}
                        path={APP_ROUTES.MERGE_LEAGUES + '/:slug'}
                    />
                    <PrivateRoute
                        component={LeagueSettings}
                        path={APP_ROUTES.LEAGUE_SETTINGS + '/:slug'}
                    />
                </Router>
            </main>
        </>
    );
};

export default App;
