import React from 'react';
import { navigate } from 'gatsby';
import { RouteComponentProps } from '@reach/router';
import { UserState, useUserStore } from '../store/user';
import { Loading } from './Loading';
import { APP_ROUTES } from '../constants';
import VerifyEmail from './VerifyEmailMessage';
import { useLeagueStore } from '../store/league';

type Props = RouteComponentProps & {
    component: React.FC;
};

const PrivateRoute: React.FC<Props> = ({ component: Component, location, ...rest }) => {
    const { user, loading: userLoading } = useUserStore((state: UserState) => {
        return {
            user: state.user,
            loading: state.loading,
        };
    });

    const {
        loading: leaguesLoading,
        leagues,
        getLeagues,
    } = useLeagueStore((state) => {
        return {
            loading: state.loading,
            leagues: state.leagues,
            getLeagues: state.getLeagues,
        };
    });

    React.useEffect(() => {
        // initially fetches leagues
        console.log(leagues);
        if (user && leagues === null && leaguesLoading === false) {
            getLeagues(user);
        }
    }, [user, leagues, leaguesLoading]);

    if (userLoading || leaguesLoading) return <Loading />;

    if (!user && location?.pathname !== APP_ROUTES.LOGIN) {
        navigate(APP_ROUTES.LOGIN);
        return null;
    }

    if (user?.emailVerified === false) {
        return <VerifyEmail />;
    }

    return <Component {...(rest as any)} />;
};

export default PrivateRoute;
