import React from 'react';
import * as Ariakit from '@ariakit/react';

interface Props {
    button: React.ReactNode;
    children: React.ReactNode;
}

export const PopoverMenu: React.FC<Props> = ({ button, children }) => {
    const menu = Ariakit.useMenuStore();

    return (
        <>
            <Ariakit.MenuButton store={menu}>{button}</Ariakit.MenuButton>
            <Ariakit.Menu
                store={menu}
                className="flex flex-col py-2 bg-white rounded shadow-md [&>[role='menuitem']]:p-1.5 [&>[role='menuitem']]:px-4 [&>[role='menuitem']:hover]:bg-gray-100 [&>[role='menuitem']:hover]:cursor-pointer"
            >
                {children}
            </Ariakit.Menu>
        </>
    );
};
