import React from 'react';
import { LeagueForm, LeagueFormState } from './index';
import { useLeagueStore } from '../../store/league';
import { UserState, useUserStore } from '../../store/user';
import { getFirebase } from '../../lib/firebase';
import { LeagueInput, Platform } from '../../types/League';

interface Props {
    onSubmit?: () => void;
}

type ClaimLeague = {
    message: string;
    show: boolean;
    leagueid?: string;
    platform?: string;
};

export const AddAndClaimLeagueForm: React.FC<Props> = ({ onSubmit }) => {
    const firebase = getFirebase();
    const [error, setError] = React.useState('');
    const { createLeague, canLeagueBeClaimed, updateLeague } = useLeagueStore((state) => {
        return {
            createLeague: state.createLeague,
            canLeagueBeClaimed: state.canLeagueBeClaimed,
            updateLeague: state.updateLeague,
        };
    });
    const user = useUserStore((state: UserState) => state.user);
    const [claimLeague, setClaimLeague] = React.useState<ClaimLeague>({
        message: '',
        show: false,
    });

    async function handleSubmit(formState: LeagueFormState) {
        try {
            await createLeague(formState);
            if (onSubmit) {
                onSubmit();
            }
        } catch (err) {
            const canBeClaimed = await canLeagueBeClaimed(formState.leagueid, formState.platform);
            if (canBeClaimed) {
                setClaimLeague({
                    message: `The league ${formState.platform}-${formState.leagueid} already exists. Would you like to claim it?`,
                    show: true,
                    leagueid: formState.leagueid,
                    platform: formState.platform,
                });
            } else {
                setError(err.message);
            }
        }
    }

    async function handleClaimLeague() {
        const { leagueid, platform } = claimLeague;
        if (leagueid && platform && user?.uid) {
            try {
                await updateLeague(
                    `${platform}-${leagueid}`,
                    {
                        leagueid: leagueid,
                        platform: platform as Platform,
                        userId: user.uid,
                        createdAt: firebase.firestore.Timestamp.fromDate(new Date()),
                    },
                    { leagueid, platform: platform as Platform } as LeagueInput
                );
                if (onSubmit) {
                    onSubmit();
                }
            } catch (err) {
                setError(err.message);
                setClaimLeague({
                    message: '',
                    show: false,
                });
            }
        }
    }

    function cancelClaimLeague() {
        setClaimLeague({
            message: '',
            show: false,
        });
    }

    return (
        <>
            <div className={`${claimLeague.show ? 'hidden' : 'block'}`}>
                <LeagueForm
                    title="Add League"
                    onSubmit={handleSubmit}
                    standalone
                    localAlertText={error}
                    sendNotification
                />
            </div>
            {claimLeague.show && (
                <div className="flex flex-col items-center max-w-sm gap-5 p-3 text-center">
                    <p>{claimLeague.message}</p>
                    <div className="flex gap-3">
                        <button className="base-button" onClick={handleClaimLeague}>
                            Claim League
                        </button>
                        <button
                            className="base-button base-button--danger"
                            onClick={cancelClaimLeague}
                        >
                            Cancel
                        </button>
                    </div>
                </div>
            )}
        </>
    );
};
