import React from 'react';
import { useEffect, useRef } from 'react';
import { useOnClickOutside } from '../hooks/useOnClickOutside';

interface Props extends React.HTMLAttributes<HTMLDialogElement> {
    isOpened: boolean;
    onProceed?: () => void;
    onClose: () => void;
    children: React.ReactNode;
}

export const DialogModal: React.FC<Props> = ({
    isOpened,
    onProceed,
    onClose,
    children,
    ...rest
}) => {
    const ref = useRef<HTMLDialogElement>(null);
    useOnClickOutside(ref, () => onClose());

    useEffect(() => {
        if (isOpened) {
            if (ref.current && ref.current.showModal) {
                ref.current.showModal();
            }
            document.body.classList.add('modal-open'); // prevent bg scroll
        } else {
            if (ref.current && ref.current.close) {
                ref.current.close();
            }
            document.body.classList.remove('modal-open');
        }
    }, [isOpened]);

    const proceedAndClose = () => {
        // onProceed();
        onClose();
    };

    const preventAutoClose = (e: any) => e.stopPropagation();

    return (
        <dialog
            ref={ref}
            onCancel={onClose}
            onClick={onClose}
            className="relative p-2 rounded"
            onSubmit={preventAutoClose}
            {...rest}
        >
            <button
                aria-label="Close"
                className="absolute text-3xl right-4 top-2"
                onClick={onClose}
            >
                <span aria-hidden="true">&times;</span>
            </button>
            <div onClick={preventAutoClose}>{children}</div>
        </dialog>
    );
};
