import React from 'react';
import { League, LeagueYear } from '../../types/League';

interface YearFieldsProps {
    league: League;
    onYearChange: (leagueId: string, year: LeagueYear) => void;
}

const YearFields: React.FC<YearFieldsProps> = ({ league, onYearChange }) => {
    if (!league.years) return null;

    return (
        <div className="w-1/2">
            <h4 className="mb-2">
                Edit years for {league.platform}-{league.leagueid}
            </h4>
            {league.years.map((year) => {
                const id = `${league.id}-${year.year}`;
                return (
                    <div className="form-group form-group--horizontal" key={id}>
                        <input
                            id={id}
                            type="checkbox"
                            checked={year.enabled}
                            onChange={function () {
                                onYearChange(league.id, { ...year, enabled: !year.enabled });
                            }}
                        />
                        <label htmlFor={id}>{year.year}</label>
                    </div>
                );
            })}
        </div>
    );
};

interface Props {
    league1: League;
    league2?: League;
    onYearChange: (leagueId: string, year: LeagueYear) => void;
}

const YearsForm: React.FC<Props> = ({ league1, league2, onYearChange }) => {
    if (!league1.years && !league2?.years) return null;

    return (
        <section>
            <h3>Years</h3>
            <div className="flex flex-col gap-4 my-2 md:flex-row">
                {league1.years && <YearFields league={league1} onYearChange={onYearChange} />}
                {league2?.years && <YearFields league={league2} onYearChange={onYearChange} />}
            </div>
        </section>
    );
};

export default YearsForm;
