import React from 'react';
import { League, LeagueUser } from '../../types/League';

interface Props {
    league: League;
    onUserChange: (user: LeagueUser) => void;
}

const UsersForm: React.FC<Props> = ({ league, onUserChange }) => {
    if (!league.users?.length) return null;

    let users = league.users;
    if (league.platform !== 'merged') {
        // we remove users that are already merged from being edited here. Merged users are the ones whose id
        // appears twice in an array with all the users ids for this league
        const existingUserIds = league.users.flatMap((user) => user.ids);
        users = league.users.filter((user) => {
            if (user.ids.length === 1) {
                const timesUserIdAppearsInIds = existingUserIds.filter((id) => id === user.ids[0])
                    .length;
                return timesUserIdAppearsInIds === 1;
            } else {
                return true;
            }
        });
    }

    return (
        <section>
            <h3>Users</h3>
            <div className="flex flex-col gap-4 my-4">
                {users.map((user) => {
                    const id = user.ids.toString();
                    return (
                        <div className="form-group" key={id}>
                            <label htmlFor={id} className="sr-only">
                                Edit name of {user.name}
                            </label>
                            <input
                                id={id}
                                className="base-field"
                                onChange={(e) => {
                                    onUserChange({ name: e.target.value, ids: user.ids });
                                }}
                                value={user.name}
                                required
                            />
                        </div>
                    );
                })}
            </div>
        </section>
    );
};

export default UsersForm;
