import React from 'react';
import { useUserStore, UserState } from '../store/user';
import { Loading } from '../components/Loading';
import { MessageCard, MessageCardAlert } from '../components/MessageCard';

const VerifyEmail = () => {
    const [sendVerificationEmail, logOut, loading] = useUserStore((state: UserState) => [
        state.sendVerificationEmail,
        state.logOut,
        state.loading,
    ]);
    const [message, setMessage] = React.useState<MessageCardAlert>({
        content: '',
        type: '',
    });

    const [disabled, setDisabled] = React.useState(false);

    async function handleSendPasswordResetEmail() {
        setDisabled(true);
        try {
            await sendVerificationEmail();
            setMessage({
                content: 'Email sent! Check your inbox',
                type: 'success',
            });
        } catch (err: any) {
            setMessage({
                content: 'There was an error sending the verification email',
                type: 'error',
            });
        }
    }

    if (loading) return <Loading />;

    return (
        <main className="message-page-container page--no-navbar">
            <MessageCard logoPosition="center" alert={message}>
                <div className="flex flex-col gap-4 items-center">
                    <div className="text-center">
                        <p>To use your account, you need to verify your email address.</p>
                        <p>
                            If you haven't received the verification email, please check your spam
                            folder or{' '}
                            <button
                                onClick={handleSendPasswordResetEmail}
                                disabled={disabled}
                                className="base-button--link"
                            >
                                click here to resend
                            </button>
                        </p>
                    </div>
                    <button onClick={logOut} className="base-button--link">
                        Log Out
                    </button>
                </div>
            </MessageCard>
        </main>
    );
};

export default VerifyEmail;
