import React from 'react';
import Logo from '../images/logo.jpeg';
import { Link, navigate } from 'gatsby';
import { useUserStore, UserState } from '../store/user';
import { APP_ROUTES } from '../constants';

const AppNavbar = () => {
    const { user, logOut, loading } = useUserStore((state: UserState) => {
        return {
            user: state.user,
            logOut: state.logOut,
            loading: state.loading,
        };
    });

    function handleAddLeagueClick() {
        navigate(APP_ROUTES.DASHBOARD + `?mode=add`, {
            replace: true,
            state: {
                showAddLeagueForm: new Date(),
            },
        });
    }

    if (loading || !user) return null;

    return (
        <header className="bg-white sticky top-0" data-component="app-navbar">
            <div className="container py-3 flex justify-between items-center flex-col md:flex-row gap-3">
                <div>
                    <Link to={APP_ROUTES.DASHBOARD}>
                        <img src={Logo} alt="logo" className="object-contain h-12" />
                    </Link>
                </div>
                <ul className="flex gap-3 items-center text-sm md:text-base">
                    <li>
                        <button onClick={handleAddLeagueClick}>Add League</button>
                    </li>
                    <li>
                        <Link to={APP_ROUTES.DASHBOARD}>Dashboard</Link>
                    </li>
                    <li>{user?.displayName || user?.email}</li>
                    <li>
                        <button
                            className="hover:text-primary-500 focus:text-primary-500"
                            onClick={logOut}
                        >
                            Log Out
                        </button>
                    </li>
                </ul>
            </div>
        </header>
    );
};

export default AppNavbar;
