import React from 'react';
import { LeagueForm, LeagueFormState, LeagueFormStateConfig } from './index';
import { League } from '../../types/League';
import { useLeagueStore } from '../../store/league';
import { convertIntoComposedObject, convertIntoFlatObject, getChangedValues } from '../../utils';

interface Props {
    league: League;
    onEditEnd?: () => void;
}
export const EditLeagueForm: React.FC<Props> = ({ league, onEditEnd }) => {
    const [alertMessage, setAlertMessage] = React.useState('');
    const { updateLeague } = useLeagueStore((state) => {
        return {
            updateLeague: state.updateLeague,
        };
    });

    const config: LeagueFormStateConfig = convertIntoComposedObject(league, 'initialValue');

    async function handleSubmit(values: LeagueFormState) {
        const initialValues = convertIntoFlatObject<LeagueFormState>(config, 'initialValue');
        const changedValues = getChangedValues(initialValues, values);

        // removing deprecated fields
        delete changedValues.email;
        delete changedValues.name;

        try {
            await updateLeague(league.id, changedValues, initialValues);
            if (onEditEnd) {
                onEditEnd();
            }
        } catch (err: any) {
            setAlertMessage(err.message);
        }
    }

    return (
        <LeagueForm
            title="Edit League"
            onSubmit={handleSubmit}
            config={config}
            standalone
            localAlertText={alertMessage}
        />
    );
};
