import React from 'react';
import { League } from '../../types/League';
import { convertIntoFlatObject } from '../../utils';

export type GeneratedPartiallyMergedLeague = Pick<Partial<League>, 'leagues'> &
    Pick<League, 'slug' | 'platform' | 'consolationGames' | 'status'>;

export type LeagueFormStateConfig = {
    [K in keyof GeneratedPartiallyMergedLeague]: {
        initialValue: GeneratedPartiallyMergedLeague[K];
        disabled: boolean;
    };
};

interface Props {
    title?: string;
    config?: LeagueFormStateConfig;
    onSubmit: (mergedLeague: GeneratedPartiallyMergedLeague) => Promise<void>;
}

const MergeLeaguesForm: React.FC<Props> = ({
    title = 'Merge Leagues',
    config = {} as LeagueFormStateConfig,
    onSubmit,
}) => {
    const initialValues: GeneratedPartiallyMergedLeague = convertIntoFlatObject(
        config,
        'initialValue'
    );
    const [disabled, setDisabled] = React.useState(false);
    const [errorMessage, setErrorMessage] = React.useState('');

    async function handleSubmit(e: React.FormEvent<HTMLFormElement>) {
        setDisabled(true);
        e.preventDefault();

        const slug = (e.target as any).elements.slug.value;
        const leagueData: GeneratedPartiallyMergedLeague = {
            slug,
            platform: 'merged',
            consolationGames: false,
            status: 'generating',
        };

        try {
            await onSubmit(leagueData);
        } catch (error: any) {
            setErrorMessage(error.message);
        }
        setDisabled(false);
    }

    return (
        <div className="flex flex-col max-w-sm gap-3 p-3">
            <h2>{title}</h2>
            <form className="flex flex-col items-start gap-3" onSubmit={handleSubmit}>
                <div aria-live="polite" className="empty:hidden">
                    {errorMessage.length > 0 && <p className="text-danger-500">{errorMessage}</p>}
                </div>
                <div className="form-group">
                    <label htmlFor="slug">League Id</label>
                    <input
                        className="base-field"
                        name="slug"
                        id="slug"
                        required
                        pattern="^[a-zA-Z0-9-_]+$"
                        defaultValue={config?.slug?.initialValue || ''}
                    />
                    <label htmlFor="slug" className="base-field__info">
                        This will be the url where the new league lives. For example
                        https://fantasy-franchise.com/[league-id]
                    </label>
                </div>
                <button disabled={disabled} className="base-button">
                    Merge Leagues
                </button>
            </form>
        </div>
    );
};
export default MergeLeaguesForm;
