// Page for handling password recovery and email verification
import React from 'react';
import { MessageCard, MessageCardAlert } from '../components/MessageCard';
import { Link, navigate } from 'gatsby';
import { UserState, useUserStore } from '../store/user';
import { RouteComponentProps, useParams } from '@reach/router';
import { APP_ROUTES } from '../constants';

type Params = {
    mode?: 'resetPassword' | 'verifyEmail';
    oobCode?: string;
};

const mode: any = (
    typeof window !== 'undefined'
        ? new URLSearchParams(window.location.search).get('mode')
        : undefined
) as undefined | null | 'resetPassword' | 'verifyEmail';

const oobCode: any =
    typeof window !== 'undefined'
        ? new URLSearchParams(window.location.search).get('oobCode')
        : (null as undefined | null | string);

const EmailCodes: React.FC<RouteComponentProps> = () => {
    const [message, setMessage] = React.useState<MessageCardAlert>({
        content: '',
        type: '',
    });
    const [disabled, setDisabled] = React.useState(false);
    const { applyPasswordResetCode, applyVerificationCode } = useUserStore((state: UserState) => {
        return {
            applyPasswordResetCode: state.applyPasswordResetCode,
            applyVerificationCode: state.applyVerificationCode,
        };
    });

    async function handlePasswordReset(e: React.FormEvent<HTMLFormElement>) {
        e.preventDefault();
        if (oobCode) {
            setDisabled(true);
            const newPassword = (e.target as any).elements.password.value;
            await applyPasswordResetCode(oobCode, newPassword);
        }
    }

    React.useEffect(() => {
        async function verifyEmail() {
            if (mode === 'verifyEmail' && oobCode) {
                try {
                    await applyVerificationCode(oobCode);
                    setMessage({
                        content: 'Your email has been verified',
                        type: 'success',
                    });
                } catch (err: any) {
                    setMessage({
                        content: 'There was an error verifying your email',
                        type: 'error',
                    });
                }
            }
        }

        verifyEmail();
    }, []);

    return (
        <div className="message-page-container page--no-navbar">
            <MessageCard alert={message}>
                {mode === 'resetPassword' && (
                    <form
                        onSubmit={handlePasswordReset}
                        className="flex flex-col items-start gap-3"
                    >
                        <div className="form-group">
                            <label htmlFor="new-password">New Password</label>
                            <input
                                id="new-password"
                                className="base-field"
                                type="password"
                                name="password"
                                required
                                minLength={8}
                            />
                        </div>
                        <button className="base-button" disabled={disabled}>
                            Reset Password
                        </button>
                        {message.type === 'success' && <Link to={APP_ROUTES.LOGIN}>Log In</Link>}
                    </form>
                )}
                {mode === 'verifyEmail' && (
                    <>
                        {message.type === 'success' && (
                            <div className="flex justify-center">
                                <Link to="/dashboard" replace>
                                    Go to dashboard
                                </Link>
                            </div>
                        )}
                    </>
                )}
            </MessageCard>
        </div>
    );
};

export default EmailCodes;
