import React from 'react';
import MergeLeaguesForm, { GeneratedPartiallyMergedLeague, LeagueFormStateConfig } from './index';
import { League } from '../../types/League';
import { useLeagueStore } from '../../store/league';
import { useUserStore, UserState } from '../../store/user';
import { convertIntoComposedObject, convertIntoFlatObject, getChangedValues } from '../../utils';

interface Props {
    league: League;
    onSubmit: () => void;
}

export const EditMergedLeagueForm: React.FC<Props> = ({ league, onSubmit }) => {
    const user = useUserStore((state: UserState) => state.user);
    const { updateLeague, getLeagues } = useLeagueStore((state) => {
        return {
            updateLeague: state.updateLeague,
            getLeagues: state.getLeagues,
            leagues: state.leagues,
        };
    });

    const config: LeagueFormStateConfig = convertIntoComposedObject(league, 'initialValue');

    async function handleSubmit(partialMergedLeague: GeneratedPartiallyMergedLeague) {
        const initialValues = convertIntoFlatObject<GeneratedPartiallyMergedLeague>(
            config,
            'initialValue'
        );
        const changedValues = getChangedValues(initialValues, partialMergedLeague);
        if (Object.keys(changedValues).length === 0) {
            throw new Error('Failed updating league: no changes detected');
        } else {
            try {
                await updateLeague(league.id, changedValues, league);
                if (user) {
                    await getLeagues(user);
                }
                onSubmit();
            } catch (err: any) {
                throw err;
            }
        }
    }

    return <MergeLeaguesForm title="Edit Merged League" onSubmit={handleSubmit} config={config} />;
};
