import React from 'react';
import { RouteComponentProps, useParams } from '@reach/router';
import { useLeagueStore } from '../store/league';
import { generateDocumentLeagueId } from '../utils/leagueUtils';
import { MessageCard } from '../components/MessageCard';
import { League, LeagueUser } from '../types/League';
import { useBanner } from '../context/BannerContext';

/**
 * Puts the primaryUser as the first user in the array
 */
function sortSelectedUsers(selectedUsers: LeagueUser[], primaryUser: LeagueUser | null) {
    if (!primaryUser) return selectedUsers;
    const sortedUsers = selectedUsers.filter(
        (user) => user.ids.toString() !== primaryUser.ids.toString()
    );

    sortedUsers.unshift(primaryUser);
    return sortedUsers;
}

type MergeLeaguesParams = {
    slug: string;
};

const ErrorMessage: React.FC<{ slug: string }> = ({ slug }) => {
    return (
        <div className="message-page-container">
            <MessageCard logoPosition="none">
                <p className="text-center">
                    Error getting merge data for <strong>{slug}</strong>. If this error persists
                    contact the admin.
                </p>
            </MessageCard>
        </div>
    );
};

const MergeLeaguesLayout: React.FC<{
    slug: string;
    children: React.ReactNode;
    onClick: () => void;
    buttonLabel: string;
}> = ({ slug, children, onClick, buttonLabel }) => {
    return (
        <div className="container">
            <div className="flex flex-col items-start justify-between gap-2 md:items-center md:flex-row">
                <div>
                    <h2 className="text-xl font-semibold">Merging Dupliates</h2>
                    <h3 className="text-lg font-normal">League: {slug}</h3>
                </div>

                <button className="base-button" onClick={onClick}>
                    {buttonLabel}
                </button>
            </div>

            <div className="flex flex-col gap-6 mt-4 md:flex-row">{children}</div>
        </div>
    );
};

const LeagueCol: React.FC<{
    league: League;
    onUserClick: (user: LeagueUser) => void;
    selectedUsers: LeagueUser[];
    mergedUsers: LeagueUser[][];
    mergedUsersBeingEdited: LeagueUser[] | null;
}> = ({ league, onUserClick, selectedUsers, mergedUsers, mergedUsersBeingEdited }) => {
    // filter league.users to only include users that are not in mergedUsers but keep them if they are being edited and not in selectedUsers
    let filteredUsers = league.users?.filter((user) => {
        if (mergedUsersBeingEdited) {
            return (
                !mergedUsers
                    .flat()
                    .map((user) => user.ids.toString())
                    .includes(user.ids.toString()) ||
                mergedUsersBeingEdited
                    .map((user) => user.ids.toString())
                    .includes(user.ids.toString())
            );
        } else {
            return !mergedUsers
                .flat()
                .map((user) => user.ids.toString())
                .includes(user.ids.toString());
        }
    });

    // filter the users with more than one id in the ids array
    filteredUsers = filteredUsers?.filter((user) => user.ids.length === 1);

    const filteredLeague = { ...league, users: filteredUsers };

    return (
        <div className="merge-leagues-column">
            <h3 className="merge-leagues-column__title">
                League {generateDocumentLeagueId(league)}
            </h3>
            <ul className="merge-leagues-column__list">
                {filteredLeague.users?.map((user, i) => {
                    return (
                        <li
                            key={i}
                            className={`
                            merge-leagues-column__list-item
                            ${
                                selectedUsers
                                    .map((user) => user.ids.toString())
                                    .includes(user.ids.toString())
                                    ? 'merge-leagues-column__list-item--selected'
                                    : ''
                            }
                        `}
                        >
                            <button
                                onClick={() => onUserClick(user)}
                                className="merge-leagues-column__list-item-button"
                            >
                                {user.name}
                            </button>
                        </li>
                    );
                })}
            </ul>
        </div>
    );
};

const MergeLeagues: React.FC<RouteComponentProps> = () => {
    /**
     * This component assumes that users in the .users array of leagues with a single id are unmerged
     * and users with multiple ids are merged.
     */
    const { slug }: MergeLeaguesParams = useParams();
    const { leagues, updateLeague } = useLeagueStore((state) => {
        return {
            leagues: state.leagues,
            updateLeague: state.updateLeague,
        };
    });

    let league1: League | undefined;
    let league2: League | undefined;

    let currentSlug: string = '';
    const currentLeague = leagues?.find((league) => {
        currentSlug = league.slug || generateDocumentLeagueId(league);
        return currentSlug === slug;
    });

    const isMergedAndValid = currentLeague?.platform === 'merged' && !!currentLeague?.leagues;

    if (isMergedAndValid) {
        const [league1Id, league2Id] = currentLeague.leagues as string[];
        league1 = leagues?.find((league) => {
            return league.id === league1Id;
        });

        league2 = leagues?.find((league) => {
            return league.id === league2Id;
        });
    }

    function generateInitialMergedUsers(): LeagueUser[][] {
        if (isMergedAndValid && league1 && league2 && currentLeague?.users) {
            const result: LeagueUser[][] = [];

            for (const combination of currentLeague.users) {
                const league1Users = (league1.users as LeagueUser[]).filter((user) => {
                    return combination.ids.includes(user.ids.toString());
                });

                const league2Users = (league2.users as LeagueUser[]).filter((user) => {
                    return combination.ids.includes(user.ids.toString());
                });

                const selectedUsers = [...league1Users, ...league2Users];
                const primaryUser = { name: combination.name, ids: [combination.ids[0]] };

                result.push(sortSelectedUsers(selectedUsers, primaryUser));
            }

            return result;
        } else if (currentLeague?.platform !== 'merged' && currentLeague?.users) {
            const result: LeagueUser[][] = [];
            const mergedUsers: LeagueUser[] = [];
            const users: LeagueUser[] = [];

            for (const user of currentLeague.users) {
                if (user.ids.length > 1) {
                    mergedUsers.push(user);
                } else {
                    users.push(user);
                }
            }

            for (const combination of mergedUsers) {
                const leagueUsers = users.filter((user) => {
                    return combination.ids.includes(user.ids.toString());
                });

                result.push([...leagueUsers]);
            }

            return result;
        }

        return [];
    }

    const initiallyMergedUsers = generateInitialMergedUsers();
    const isEditingLeague = !!initiallyMergedUsers.length;

    const [selectedUsers, setSelectedUsers] = React.useState<LeagueUser[]>([]);
    const [primaryUser, setPrimaryUser] = React.useState<LeagueUser | null>(null);
    const [mergedUsers, setMergedusers] = React.useState<LeagueUser[][]>(initiallyMergedUsers);
    const [mergedUsersBeingEdited, setMergedUsersBeingEdited] = React.useState<LeagueUser[] | null>(
        null
    );
    const isEditingFinalUsers = mergedUsersBeingEdited !== null;
    const { showBanner } = useBanner();

    function handleSelectedUsersClick(leagueUser: LeagueUser) {
        const userIdsString = leagueUser.ids.toString();
        const selectedLeagueUsersString = selectedUsers.map((user) => user.ids.toString());

        if (selectedLeagueUsersString.includes(userIdsString)) {
            setSelectedUsers((prev) => {
                return prev.filter((user) => {
                    return user.ids.toString() !== userIdsString;
                });
            });
        } else {
            setSelectedUsers((prev) => {
                return [...prev, leagueUser];
            });
        }
    }

    React.useEffect(() => {
        if (!primaryUser && selectedUsers.length > 0) {
            setPrimaryUser(selectedUsers[0]);
        } else if (
            // if a primary user is selected and it is not in the selected users, then remove the primary user
            primaryUser &&
            !selectedUsers.map((user) => user.ids.toString()).includes(primaryUser.ids.toString())
        ) {
            setPrimaryUser(selectedUsers.length > 0 ? { ...selectedUsers[0] } : null);
        }
    }, [selectedUsers]);

    if (!currentLeague) {
        return (
            <div className="container">
                <p>You have not registered the league {slug}</p>
            </div>
        );
    }

    const sortedSelectedUsers = sortSelectedUsers(selectedUsers, primaryUser);
    if (isMergedAndValid || currentLeague.platform !== 'merged') {
        if (isMergedAndValid) {
            if (!league1?.users || !league2?.users) return <ErrorMessage slug={slug} />;
        } else if (currentLeague.platform !== 'merged') {
            league1 = currentLeague;
            if (!league1?.users) return <ErrorMessage slug={slug} />;
        }

        const isSortedSelectedUsersTheSameBeingEdited = () => {
            if (!mergedUsersBeingEdited) return false;

            return JSON.stringify(mergedUsersBeingEdited) === JSON.stringify(sortedSelectedUsers);
        };

        return (
            <MergeLeaguesLayout
                slug={currentSlug}
                onClick={async () => {
                    let mergedUsersCompiled = mergedUsers.map((users) => {
                        return {
                            name: users[0].name,
                            ids: users.reduce((acc, curr) => [...acc, ...curr.ids], [] as string[]),
                        };
                    });

                    if (currentLeague.platform !== 'merged' && league1) {
                        const baseUsers = (league1.users as LeagueUser[]).filter(
                            (user) => user.ids.length === 1
                        );

                        mergedUsersCompiled = [...baseUsers, ...mergedUsersCompiled];
                    }

                    await updateLeague(currentLeague.id, {
                        users: mergedUsersCompiled,
                    });

                    showBanner({
                        type: 'success',
                        text: 'Success!',
                    });
                }}
                buttonLabel={isEditingLeague ? 'Save Changes' : 'Merge'}
            >
                {league1 && (
                    <LeagueCol
                        onUserClick={handleSelectedUsersClick}
                        selectedUsers={selectedUsers}
                        league={league1}
                        mergedUsers={mergedUsers}
                        mergedUsersBeingEdited={mergedUsersBeingEdited}
                    />
                )}
                {league2 && (
                    <LeagueCol
                        onUserClick={handleSelectedUsersClick}
                        selectedUsers={selectedUsers}
                        league={league2}
                        mergedUsers={mergedUsers}
                        mergedUsersBeingEdited={mergedUsersBeingEdited}
                    />
                )}
                <div className="merge-leagues-column">
                    <h3 className="merge-leagues-column__title">Selected Users</h3>
                    <p className="px-2">Primary</p>
                    <ul className="merge-leagues-column__list">
                        {selectedUsers.map((user, i) => {
                            return (
                                <li key={i} className="relative merge-leagues-column__list-item">
                                    <button
                                        className="flex items-center merge-leagues-column__list-item-button"
                                        onClick={() => setPrimaryUser(user)}
                                    >
                                        <div
                                            className={`w-3 h-3 rounded-full mr-14
                                            ${
                                                primaryUser?.ids.toString() === user.ids.toString()
                                                    ? 'bg-primary-500'
                                                    : 'bg-gray-500'
                                            }
                                        `}
                                        />
                                        {user.name}
                                    </button>
                                    <button
                                        className="absolute flex items-center justify-center w-4 h-4 pb-0.5 text-sm text-white -translate-y-1/2 border border-white rounded top-1/2 right-2 bg-danger-500"
                                        aria-label="Delete from selected users"
                                        onClick={() => {
                                            setSelectedUsers((prev) => {
                                                return prev.filter((u) => {
                                                    return u.ids.toString() !== user.ids.toString();
                                                });
                                            });
                                        }}
                                    >
                                        <span>x</span>
                                    </button>
                                </li>
                            );
                        })}
                    </ul>
                    <div className="px-2 mt-auto">
                        <button
                            className="w-full base-button"
                            disabled={
                                selectedUsers.length < 2 ||
                                isSortedSelectedUsersTheSameBeingEdited()
                            }
                            onClick={() => {
                                setSelectedUsers([]);
                                if (isEditingFinalUsers) {
                                    setMergedusers((prev) => {
                                        return [...prev, sortedSelectedUsers].filter(
                                            (userGroup) => {
                                                return (
                                                    JSON.stringify(userGroup) !==
                                                    JSON.stringify(mergedUsersBeingEdited)
                                                );
                                            }
                                        );
                                    });
                                } else {
                                    setMergedusers((prev) => {
                                        return [...prev, sortedSelectedUsers];
                                    });
                                }
                                setMergedUsersBeingEdited(null);
                            }}
                        >
                            {isEditingFinalUsers ? 'Save Changes' : 'Merge Users'}
                        </button>
                    </div>
                </div>
                <div className="merge-leagues-column">
                    <h3 className="merge-leagues-column__title">Final Users</h3>
                    <ul className="merge-leagues-column__list">
                        {mergedUsers.map((userGroup, i) => {
                            return (
                                <li key={i} className="relative merge-leagues-column__list-item">
                                    <button
                                        className="flex items-center merge-leagues-column__list-item-button"
                                        onClick={() => {
                                            // if this is the currentlybiengedited group make selectedusers null and set the mergedusersbeingedited to null
                                            // else make the userGroup the selectedUsers
                                            const isCurrentlyBeingEdited = mergedUsersBeingEdited
                                                ? userGroup.every((user) => {
                                                      return mergedUsersBeingEdited
                                                          .map((u) => u.ids.toString())
                                                          .includes(user.ids.toString());
                                                  })
                                                : false;

                                            if (isCurrentlyBeingEdited) {
                                                setSelectedUsers([]);
                                                setMergedUsersBeingEdited(null);
                                            } else {
                                                setSelectedUsers(userGroup);
                                                setMergedUsersBeingEdited(userGroup);
                                            }
                                        }}
                                    >
                                        <ul className="w-full p-2 bg-gray-100 rounded">
                                            {userGroup.map((user, j) => {
                                                return (
                                                    <li
                                                        key={j}
                                                        className={`flex items-center ${
                                                            j === 0
                                                                ? 'text-primary-500 font-semibold'
                                                                : ''
                                                        }`}
                                                    >
                                                        {user.name}
                                                    </li>
                                                );
                                            })}
                                        </ul>
                                    </button>
                                    <button
                                        className="absolute flex items-center justify-center w-4 h-4 pb-0.5 text-sm text-white -translate-y-1/2 border border-white rounded top-6 right-4 bg-danger-500"
                                        aria-label="Delete from merged users"
                                        onClick={() => {
                                            setMergedusers((prev) => {
                                                return prev.filter((u) => {
                                                    return (
                                                        u
                                                            .map((user) => user.ids.toString())
                                                            .join(',') !==
                                                        userGroup
                                                            .map((user) => user.ids.toString())
                                                            .join(',')
                                                    );
                                                });
                                            });

                                            // if the userGroup is the one being edited, clear the selected users and make the editing null
                                            if (
                                                mergedUsersBeingEdited &&
                                                mergedUsersBeingEdited
                                                    .map((user) => user.ids.toString())
                                                    .join(',') ===
                                                    userGroup
                                                        .map((user) => user.ids.toString())
                                                        .join(',')
                                            ) {
                                                setSelectedUsers([]);
                                                setMergedUsersBeingEdited(null);
                                            }
                                        }}
                                    >
                                        <span>x</span>
                                    </button>
                                </li>
                            );
                        })}
                    </ul>
                </div>
            </MergeLeaguesLayout>
        );
    } else {
        return <ErrorMessage slug={slug} />;
    }
};

export default MergeLeagues;
